.header__container {
    display: flex;
    align-items: center;
    color: #fff;
    font-family: Rationale;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 75% */
    text-transform: uppercase;
}

.header__nav {
    display: flex;
    color: #fff;
    font-family: Rationale;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 75% */
    text-transform: uppercase;
}

.header__link {
    text-decoration: none;
    width: 209px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 300ms;
}

.active {
    text-decoration: underline;
}

.header__link:visited {
    color: white;
}

.header__link:hover {
    color: #be4787;
}

.header__splitline {
    border-right: 3px solid rgba(255, 255, 255, 0.2);
}

.header__button {
    width: 200px;
    height: 50px;
    font-size: 16px;
}

.header__nav_unauth {
    gap: 35px;
}

.header__button_unauth {
    width: 70px;
    height: 50px;
    font-size: 16px;
}
